*{
  padding:0;
  margin:0;
}
ul,li,a{
  text-decoration: none;
  list-style: none;
  color:inherit;
}
img{
  width:100%;
}
h2>p{
  display:inline-block;
  border-bottom:5px solid #f7e0bf;
}
.wrap{
  padding-left:10rem;
}
/* header */

header{
  width:100%;
  position:fixed;
  top:0;
  left:0;
  z-index: 1000;
  padding:0 0.88rem;
}
h1{
  font-size:1.5rem;
  float:left;
  cursor: pointer;
}
nav>ul{
  display:flex;
  padding-left:2rem;
}
nav>ul>li{
  padding-left:3rem;
  padding-top:0.3rem;
}

/* article */
article{
  position:absolute;
  top:0;
  bottom:0;
  left:0;
  z-index:999;
  padding:5rem 0.88rem 0;
  background-color: #f7e0bf;
}
dd{
  padding-top:0.5rem;
}

/* about */
#about{
  padding-top:5rem;
  display:table;
  margin:0 auto;
}
#about>h2{
  margin:0 auto;
  text-align: center;
  padding:1rem 0;
}
#about>dl>dd{
  padding-left:5rem;
}
#about>dl>dd>ul>li{
  padding-bottom:1rem;
}
.Tbox{
  display:flex;
  padding-top:2rem;
}
dt.title{
  width:20rem;
  padding:1rem;
  box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
}

/* portfolio */
#portfolio{
  position:relative;
  width:100%;
  padding-top:4rem;
  box-sizing: border-box;
  display:table;
  text-align: center;
}
.card{
  position:absolute;
  width:18.75rem;
  height:25rem;
  box-sizing: border-box;
  padding:1rem;
  box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
  text-align: left;
  font-size:1rem;
  cursor: pointer;
  top:8rem;
}
.card:hover{
  top:7.8rem;
}
.card01:hover{
left:16.3rem;
}
.card02:hover{
  left:36.3rem;
  }
.card03:hover{
  left:56.3rem;
  }
.card01{
  left:16.5rem;
}
.card02{
  left:36.5rem;
}
.card03{
  left:56.5rem;
}
p.img{
  width:100%;
  border: 1px solid #000;
  margin:0 auto 1rem;
  padding:1rem;
  box-sizing: border-box;
  overflow: hidden;
}
.spanText{
  font-weight:bold;
}
.more{
  position:absolute;
  bottom:3%;
  right:1rem;
}

/* blog */
#blog{
  padding-top:3rem;
  padding-left:13rem;
  text-align: left;
}
#blog>h2>p{
  width:70%;
  box-sizing: border-box;
}
.blogBox{
  width:70%;
  padding-top:0.3rem;
}
.blogBox>li{
  border-bottom:1px solid #000;
  width:100%;
  position: relative;
  padding-top:1.5rem;
  display:flex;
  text-align: center;
}
.date{
  padding-right:5rem;
}

/* books */
#books{
  padding-top:5rem;
  text-align:left;
  padding-left:25rem;
}
.booksNav{
  display:flex;
  vertical-align: middle;
  justify-items: center;
  padding-bottom:1rem;
}
.booksNav>li{
  padding-top:2rem;
  padding-left:2rem;
  text-align: center;
}
a:hover{
  width:100%;
  background-color: #f7e0bf;
}
.active{
  background-color: #f7e0bf;
  font-weight: bold;
}
.contants{
  display:flex;
}
.imgBox{
  width:15rem;
  height:15rem;
  border: 2px solid #949494;
  box-sizing: border-box;
  position:relative;
}
.main{
  width:60%;
  padding-top:1rem;
  margin: 0 auto;
  box-sizing: border-box;
}
.booksPic{
  position:absolute;
  bottom:-50%;
  width:100%;
  display:flex;
}
.booksPic>li{
  display: inline-block;
  width:15rem;
 text-align: center;
 border: 1px solid #000;
 padding:0.5rem;
 font-size:0.88rem;
 line-height: 2rem;
 color:#949494;
 cursor: pointer;
}
.booksPic>li:not(:last-child){
  margin-right:0.3rem;
}
.text{
  padding-left:3rem;
}
.text>p{
  font-size:0.9rem;
}
.dcBox{
  display: flex;
  position:relative;
  padding-top:1rem;
}
.dc{
  
  font-size:2.5rem;
  font-weight:bold;
  color:#fabb5c;
}
.won{
  padding-left:1rem;
}
.won>dt{
  text-decoration:line-through;
  color:#949494;
  display: inline-block;
  font-size: 0.88rem;
  line-height: 0.88rem;
}
.won>dd{
  font-size:1.2rem;
  line-height: 1.2rem;
  padding-top:0;
}
.numBox{
  display:flex;
  position:absolute;
  bottom:0.3rem;
  right:-2rem;
}
.num{
  border-radius: 50%;
  border:1px solid #000;
  width:1.5rem;
  height:1.5rem;
  font-size:1.2rem;
  text-align: center;
  cursor: pointer;
}
.point{
  width:3rem;
  height:1rem;
  border:1px solid #000;
  padding:0.3rem 0;
  text-align: center;
  line-height: 1rem;
  margin:0 0.5rem;
}
.service{
  width:100%;
  background: #ccc;
  padding:1rem;
  font-size:0.8rem;
}
.service>li{
  padding-bottom:1rem;
  padding-right:4rem;
}
.payBox{
  position: relative;
}
.paybtn{
  position: absolute;
  top:0.3rem;
  right:-2rem;
}
.All>img{
  padding-top:1rem;
  padding-bottom:1rem;
}
.allMain{
  width:80%;
  padding-top:3rem;
}